* {
  box-sizing: border-box;
}

body,
html {
  background: white;
  font-size: 10px;
  margin: 0;
  font-family: Manrope, sans-serif;
}

h1,
h2 {
  font-family: DynaPuff, sans-serif;
}

.header {
  position: fixed;
  padding: 27px 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.5s;
}
.header__logo-link {
  display: block;
  text-decoration: none;
  width: 56px;
  height: 56px;
}
.header__nav {
  height: 0;
  transition: all 0.5s;
  overflow: hidden;
  background-color: black;
  position: absolute;
  left: 0;
  top: 110px;
  width: 100%;
}
.header__nav-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid white;
  border-radius: 20px;
  width: 56px;
  height: 56px;
}
.header__nav--visible {
  height: 250px;
}
.header--scroll {
  background-color: rgb(0, 0, 0);
  z-index: 999;
}
.header__menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 0 30px;
  gap: 12px;
}
.header__menu-item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 42px;
  border: 1px solid white;
  border-radius: 20px;
  padding: 0 16px;
}
.header__menu-icon {
  margin-right: 12px;
}
.header__menu-link {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #374957;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: white;
}

.container {
  padding: 0 17px;
  display: flex;
  flex-direction: column;
}
.container--header {
  flex-direction: row;
  justify-content: space-between;
}

.banner {
  background: linear-gradient(253deg, #ffee32 -13.48%, #d6d6d6 125.48%);
  height: 100vh;
  width: 100%;
}
.banner__container {
  justify-content: flex-end;
  height: 100%;
  width: 250px;
}
.banner__title {
  color: white;
  font-family: DynaPuff;
  font-size: 36px;
  margin-bottom: 150px;
  line-height: normal;
}

.meet {
  background: #202020;
  overflow: hidden;
}
.meet__container {
  padding-top: 75px;
  padding-bottom: 75px;
  position: relative;
}
.meet__container::after {
  content: "";
  width: 167.488px;
  height: 319.978px;
  transform: rotate(-45deg);
  border-radius: 70%;
  border: 1px solid #737373;
  position: absolute;
  margin-left: 350px;
  margin-top: 45px;
}
.meet__container::before {
  content: "";
  width: 167.488px;
  height: 319.978px;
  transform: rotate(45deg);
  border-radius: 70%;
  border: 1px solid #737373;
  position: absolute;
  margin-left: 350px;
  margin-top: 45px;
}
.meet__title {
  color: #faeb49;
  font-family: DynaPuff;
  font-size: 36px;
  line-height: normal;
  margin: 0 0 40px 0;
}
.meet__text {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  line-height: normal;
  margin: 0 50px 40px 0;
}
.meet__counter {
  display: flex;
}
.meet__counter-col {
  flex: 1 1 33.3333%;
}
.meet__number {
  color: #fff;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.meet__number-text {
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  max-width: 50%;
}

.app {
  background: #3c3c3c;
  padding: 53px 0 85px;
}
.app__rectangle {
  border-radius: 24px;
  background: #fffef2;
  margin-bottom: 52px;
  width: 100%;
  padding: 16px;
}
.app__rectangle-foto {
  position: relative;
  display: block;
  width: 100%;
}
.app__rectangle-foto img {
  border-radius: 24px;
  aspect-ratio: 1/1;
  width: 100%;
  height: auto;
}
.app__rectangle:nth-child(n+3) {
  display: none;
}
.app__rectangle-foto::after {
  content: url("../images/app/btn.svg");
  background: white;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 28px;
  width: 56px;
  height: 56px;
  top: 25px;
  right: 25px;
}
.app__rectangle-title {
  color: #565656;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.app__rectangle-title--small {
  color: #565656;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.app__rectangle-text {
  color: #000;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.app__rectangle-text--small {
  color: #000;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.design {
  background: #202020;
}
.design__title {
  color: #fff;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-top: 58px;
  margin-bottom: 28px;
}
.design__title-foto {
  margin-bottom: 20px;
  border-radius: 50px;
  position: relative;
  z-index: 0;
}
.design__title-foto--svg1 {
  width: 172px;
  height: 172px;
  position: absolute;
  top: 29%;
  left: 29%;
  z-index: 0;
}
.design__title-foto--svg2 {
  width: 172px;
  height: 172px;
  position: absolute;
  top: 29%;
  left: 29%;
  z-index: 0;
}
.design__title-foto--svg3 {
  width: 172px;
  height: 172px;
  position: absolute;
  top: 29%;
  left: 29%;
  z-index: 0;
}
.design__title-foto img {
  aspect-ratio: 1/1;
  width: 100%;
  height: auto;
  border-radius: 24px;
}
.design__title-foto:last-child {
  margin-bottom: 93px;
}

.bg {
  background: url("../images/bg/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 180px 0 180px;
}
.bg-text {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.footer {
  background: #202020;
  padding: 53px 0 30px;
}
.footer__container {
  margin-bottom: 40px;
}
.footer__btn {
  border: 1px solid white;
  border-radius: 140px;
  padding: 15px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.footer__btn-img {
  margin-right: 20px;
}
.footer__btn-qustion {
  color: #fff;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding: 0;
  margin: 0 0 10px 0;
}
.footer__btn-coll {
  color: #fff;
  text-align: center;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin: 0;
  display: flex;
  align-items: center;
}
.footer__btn-coll svg {
  margin-left: 10px;
}
.footer__btn-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.footer__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 70px;
  gap: 30px;
}
.footer__text2 {
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 40px;
}

/***** tablet */
@media screen and (min-width: 768px) {
  .container {
    padding: 0 34px;
  }
  .banner__title {
    font-size: 80px;
    margin-bottom: 150px;
  }
  .meet__counter {
    max-width: 660px;
  }
  .meet__counter-col {
    flex: 1 1 25%;
  }
  .meet__container {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .meet__container::after {
    left: 55%;
    width: 268px;
    height: 512px;
    top: -70px;
    border: 1px solid #737373;
  }
  .meet__container::before {
    left: 55%;
    width: 268px;
    height: 512px;
    top: -70px;
    border: 1px solid #737373;
  }
  .meet__title {
    font-size: 48px;
  }
  .meet__text {
    font-size: 16px;
    width: 50%;
  }
  .meet__number {
    font-size: 48px;
  }
  .meet__number-text {
    max-width: none;
  }
  .container--app {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .app__rectangle {
    width: calc(50% - 20px);
  }
  .app__rectangle-foto {
    border-radius: 40px;
  }
  .app__rectangle-title {
    font-size: 32px;
    margin-bottom: 22px;
  }
  .app__rectangle:nth-child(n-1) {
    display: block;
  }
  .app__rectangle:first-child {
    width: 100%;
    margin-bottom: 100px;
  }
  .app__rectangle[data-order="1"] {
    order: 10;
  }
  .app__rectangle[data-order="2"] {
    order: 30;
  }
  .app__rectangle[data-order="3"] {
    order: 20;
  }
  .app__rectangle[data-order="4"] {
    display: none;
  }
  .container--design {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .design__title {
    padding-bottom: 38px;
  }
  .design__container:first-child {
    width: 100%;
    margin-bottom: 100px;
  }
  .design__title {
    font-size: 80px;
    padding-top: 82px;
    margin-bottom: 32px;
  }
  .design__title-foto {
    width: calc(50% - 20px);
  }
  .design__title-foto:first-child {
    width: 100%;
    margin-bottom: 100px;
  }
  .design__title-foto--svg1 {
    width: 227px;
    height: 227px;
    top: 40%;
    left: 40%;
  }
  .design__title-foto--svg1 svg {
    width: 100%;
    height: 100%;
  }
  .design__title-foto--svg2 {
    width: 147px;
    height: 151px;
  }
  .design__title-foto--svg2 svg {
    width: 100%;
    height: 100%;
    top: 35%;
    left: 35%;
  }
  .design__title-foto--svg3 {
    width: 183px;
    height: 177px;
  }
  .design__title-foto--svg3 svg {
    width: 100%;
    height: 100%;
    top: 40%;
    left: 40%;
  }
  .bg-text {
    font-size: 48px;
    width: 85%;
    margin: auto;
  }
  .footer {
    padding: 96px 0 30px;
  }
  .footer__btn-img {
    margin-right: 20px;
    width: 108px;
    height: 108px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .footer__btn-img svg {
    width: 100%;
    height: 100%;
  }
  .footer__btn-qustion {
    font-size: 48px;
  }
  .footer__btn-coll {
    font-size: 24px;
  }
  .footer__btn-coll svg {
    margin-left: 24px;
    width: 55px;
    height: 26px;
  }
  .footer__btn-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}
/***** desktop */
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1084px;
    margin: 0 auto;
    width: 100%;
  }
  .header {
    padding: 18px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__menu {
    display: flex;
    flex-direction: row;
    height: 56px;
    padding: 0;
  }
  .header__nav {
    position: relative;
    top: auto;
    height: 56px;
    background-color: transparent;
  }
  .header__nav-button {
    display: none;
  }
  .banner__container {
    width: 100%;
  }
  .bannerbanner__container {
    width: 100%;
  }
  .banner__title {
    font-size: 80px;
    margin-bottom: 119px;
    width: 100%;
  }
  .meet__container {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .meet__text {
    width: 45%;
  }
  .meet__container::after {
    width: 268px;
    height: 512px;
  }
  .meet__container::before {
    width: 268px;
    height: 512px;
  }
  .app__rectangle {
    width: calc(33.3333% - 26.667px);
  }
  .app__rectangle[data-order="2"] {
    order: 30;
  }
  .app__rectangle[data-order="3"] {
    order: 20;
  }
  .app__rectangle[data-order="4"] {
    display: block;
    order: 40;
  }
  .app__title {
    font-size: 40px;
  }
  .footer__icon {
    padding-top: 150px;
    gap: 34px;
  }
  .footer__btn {
    padding: 42px 137px;
    max-width: 914px;
  }
  .footer__btn-img {
    margin-right: 45px;
  }
  .design__title-foto {
    width: calc(50% - 20px);
  }
  .design__title-foto:first-child {
    width: 100%;
    margin-bottom: 100px;
  }
  .design__title-foto--svg1 {
    width: 268px;
    height: 268px;
    top: 40%;
    left: 40%;
  }
  .design__title-foto--svg1 svg {
    width: 100%;
    height: 100%;
  }
  .design__title-foto--svg2 {
    width: 187px;
    height: 191.493px;
  }
  .design__title-foto--svg2 svg {
    width: 100%;
    height: 100%;
    top: 35%;
    left: 35%;
  }
  .design__title-foto--svg3 {
    width: 233px;
    height: 225px;
  }
  .design__title-foto--svg3 svg {
    width: 100%;
    height: 100%;
    top: 40%;
    left: 40%;
  }
}